.about {
  &__title {
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    margin-bottom: 32px;
    text-align: left;

    @include onTablet {
      font-size: 48px;
    }

    @include onDesktop {
      font-size: 64px;
    }
  }

  &__content {
    font-family: "Inter", sans-serif;
    font-size: 24px;
    line-height: 140%;
    text-align: left;
  }
}
